<template>
  <div>
    <div class="md-layout" style="display: block; color:#F6F6F6">
      <div class="supportermain" style="min-height: 600px;">
        <div v-for="supporterinfo in  supporterlists" :key="supporterinfo.name">
          <md-card
            style="width: 50%; margin: 4px;marginTop:100px; display: inline-block; vertical-align: top;"
          >
            <md-card-header>
              <md-card-header-text>
                <div class="md-title">
                  {{supporterinfo.name}}
                  <br />
                  <br />
                </div>
                <div class="md-subhead" style="text-align:left;font-weight: bold;">
                  # 이메일: {{supporterinfo.email}}
                  <br />
                  # 연락처: {{supporterinfo.contact}}
                  <br />
                  # 설명글: {{supporterinfo.text}}
                  <br />
                </div>
              </md-card-header-text>
              <md-card-media md-medium>
                <!-- <img v-bind:src="supporterinfo.photo_path" /> -->
              </md-card-media>
            </md-card-header>
            <md-card-actions>
              <md-button class="md-raised md-accent">Decline</md-button>
              <md-button class="md-raised md-primary">Accept</md-button>
            </md-card-actions>
          </md-card>
        </div>

        <!-- =============서포터즈 submit 양식 ================== -->
        <md-dialog :md-active.sync="active" style="width : 500px; height: 1000px ">
          <md-dialog-title>서포터즈 신청</md-dialog-title>
          <md-field class="select">
            <label>신청자 이름</label>
            <md-input v-model="submit.name"></md-input>
          </md-field>
          <md-field class="select">
            <label>신청자 이메일</label>
            <md-input v-model="submit.email"></md-input>
          </md-field>
          <md-field class="select">
            <label>신청자 연락처</label>
            <md-input v-model="submit.contact"></md-input>
          </md-field>
          <md-field class="select">
            <label>사진</label>
            <md-file v-model="submit.photo_path" accept="image/*" />
          </md-field>
          <md-field class="select">
            <label>설명글</label>
            <md-textarea v-model="submit.text"></md-textarea>
          </md-field>
          <md-dialog-actions>
            <md-button class="md-primary" v-on:click="active = false">Close</md-button>
            <md-button class="md-primary" v-on:click="Register">Submit</md-button>
          </md-dialog-actions>
        </md-dialog>
        <!-- =============================== -->
        <md-speed-dial :class="bottomPosition" style="marginRight:20%; marginBottom:8%">
          <md-button class="md-fab md-primary" v-on:click="active = true">
            <img src="../assets/icon-add.svg" />
          </md-button>
        </md-speed-dial>
      </div>
      <div class="md-layout-item" style="backgroundColor:#FFF; height:30px;"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bottomPosition: 'md-bottom-right',
      active : false,
      supporterlists : {},
      submit:{
        name : "",
        email: "",
        contact : "",
        text :  "",
        photo_path : "",
      }
    };
  },
created() {
          this
            .$http
            .get('/api/supporter')
            .then(response => {
              this.supporterlists = response.data; 
              console.log("Success")
              console.log(this.supporterlists)
            })
          },
        
  methods: {
      Register(){
        this.$http
              .post("/api/supporter/signup", this.submit)
              .then(res => {
              console.log("Sumit Success");
              })
              .catch(err => {
              console.log("Submit failed");
              });
        this.active = false;
      }

  }
};
</script>

<style lang="css">
 .md-dialog {
    max-width: 768px;
  }
   .demo-option {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
   }

    .select {
      margin: 15px;
      display: inline-flex;
      width: auto;
    }
  
  
</style>



